*{
  margin:0;
  padding:0;
}

.container{
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(40,40,40);
}
.board-container{
  width:600px;
  height: 600px;
}
.board{
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
}

.square{
  width:12.5%;
  height: 12.5%;
}

.promote-square{
  width:50%;
  height: 50%;
}

.squared-white{
  background-color:#eedc97;
}

.squared-black{
  background-color: #964d22;
}

.board-square{
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

}

.piece-image{
  width: 100%;
  cursor:pointer
}

.vertical-heading{
  text-orientation: upright;
  writing-mode: vertical-lr;
  font-family: sans-serif;
  padding: 10px;
  color: white;
}

.vertical-heading button{
    border:2px solid white;
    margin-top: 20px;
    cursor: pointer;
    background: rgb(63,63,63);
    border-radius: 10px;
}